import React from "react";
import  ReactDOM from "react-dom";
import Card from "./Cards";
import Sdata from './Sdata';
import "./index.css";

//console.log(Sdata[0].sname);

function ncard(val,index,Sdata){
  console.log(val,index,Sdata);
  return(
    <Card
    imgsrc={val.imgsrc}
    title ={val.title}
    sname= {val.sname}
    links = {val.links}
    />
  );
  
}

ReactDOM.render(
<>

<h1 className='heading_style'> List of Top 6 Netflix Series in 2021</h1>
<h3 className='heading_style'>Created By @Dheeman</h3>

{Sdata.map(ncard)};
</>,
  document.getElementById('root')
);



